body {
	font-family: "ProximaNova", Arial, sans-serif;
	font-weight: 400;
	font-size: 14px;
	color: #2b3f64;

	max-width: 1920px;
	margin: 0 auto;

	background-color: #ffffff;

	@media (min-width: 1100px) {
		font-size: 16px;
	}
}

.visually-hidden {
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;

	position: absolute;
	border: 0;
	clip: rect(0 0 0 0);
	overflow: hidden;
}

.container {
	max-width: 1604px;
	margin: 0 auto;
	padding: 0 16px;

	@media (min-width: 1604px) {
		padding: 0;
	}
}

.swiper-button-prev,
.swiper-button-next {
	width: 40px;
	height: 40px;

	border: 2px solid #d7dee9;
	box-sizing: border-box;
	border-radius: 2px;

	background-size: 14px 14px;
}

.btn {
	background-color: #365dd6;
	border-radius: 4px;

	font-weight: 600;
	font-size: 18px;
	line-height: 22px;
	color: #ffffff;
}

.main-title {
	color: #2b3f64;
	font-weight: 600;
	font-size: 20px;
	line-height: 25px;
	text-align: center;

	@media (min-width: 1100px) {
		font-size: 32px;
		line-height: 45px;
	}
}

.logo {
	display: flex;
	align-items: center;
	margin-right: 8px;

	@media (min-width: 1100px) {
		width: 320px;
		margin-right: 0px;
	}
}

.logo__img {
	max-width: 25px;
	width: 100%;
	height: 30px;
	margin-right: 8px;

	@media (min-width: 1100px) {
		max-width: 44px;
		height: 55px;
	}
}

.logo__title p {
	&:last-child {
		display: none;

		@media (min-width: 1100px) {
			display: block;

			font-weight: bold;
			font-size: 16px;
			line-height: 19px;
			color: #ff3636;
		}
	}

	&:first-child {
		color: #ff3636;
		font-weight: 800;
		font-size: 22px;
		line-height: 27px;

		span {
			color: #365dd6;
		}

		@media (min-width: 1100px) {
			font-size: 22px;
			line-height: 27px;
		}
	}
}

//!===============HEADER===============//
.header {
}

.header-top {
	position: relative;

	@media (min-width: 1100px) {
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex-wrap: wrap;
		box-shadow: 2px 2px 15px 2px rgba(56, 55, 121, 0.08);

		padding: 14px 16px;
	}

	@media (min-width: 1920px) {
		padding-right: 210px;
		padding-left: 158px;
	}
}

.header .city {
	font-style: normal;
	font-weight: 300;
	font-size: 16px;
	line-height: 19px;
	color: #2b3f64;
	letter-spacing: 0.5px;

	position: relative;

	padding-left: 27px;

	&::before {
		content: "";

		position: absolute;
		top: 0;
		left: 0;
		transform: translate(0, 0);

		background-position: 0 0;
		background-repeat: no-repeat;
		background-image: url("../img/src/pin.png");

		width: 20px;
		height: 20px;
	}
}

.header-left {
	background-color: #ffffff;
	display: flex;
	align-items: center;
	justify-content: space-between;
	position: relative;
	z-index: 2;

	@media (max-width: 1099px) {
		padding: 9px 16px;
		margin-bottom: 9px;

		box-shadow: 2px 2px 15px 2px rgba(56, 55, 121, 0.08);
	}
}

.header-left button {
	background-color: transparent;
	background-position: center;
	background-repeat: no-repeat;
	background-image: url("../img/src/burger.png");

	width: 30px;
	height: 30px;

	font-size: 0;
	cursor: pointer;

	@media (min-width: 1100px) {
		display: none;
	}
}

.header-left .header-close {
	background-image: url("../img/src/close.png");
}

.header-left .call {
	font-size: 0;

	background-position: center;
	background-repeat: no-repeat;
	background-image: url("../img/src/phone-white.png");
	background-color: #365dd6;

	width: 32px;
	height: 34px;

	@media (min-width: 1100px) {
		display: none;
	}
}

.header-right {
	@media (max-width: 1099px) {
		max-width: 320px;
		padding-top: 28px;
		padding-bottom: 41px;
		padding-left: 27px;
		padding-right: 16px;

		display: none;
		background-color: #6196c9;
		position: absolute;
		top: 53px;
		left: 0;
		z-index: 100;

		color: #ffffff;
	}

	@media (min-width: 1100px) {
		width: 100%;
	}

	@media (min-width: 1280px) {
		width: auto;
	}
}

.header .selectric {
	display: none;

	@media (min-width: 1100px) {
		display: block;
		background-color: #ff3636;
		border: none;
		border-radius: 4px;
		position: relative;

		height: 52px;

		&::before {
			content: "";

			position: absolute;
			top: 50%;
			left: 16px;
			transform: translate(0, -50%);

			background-position: 0 0;
			background-repeat: no-repeat;
			background-image: url("../img/src/gps.png");

			width: 16px;
			height: 16px;
		}

		&::after {
			content: "";

			position: absolute;
			top: 50%;
			right: 16px;
			transform: translate(0, -50%);

			background-position: 0 0;
			background-repeat: no-repeat;
			background-image: url("../img/src/down.png");

			width: 24px;
			height: 24px;
		}
	}
}

.header .selectric-items {
	@media (min-width: 1100px) {
		background-color: #ff3636;
		border: none;

		li {
			color: #ffffff;
			font-family: ProximaNova;
			font-weight: 600;
			font-size: 16px;
		}
	}
}

.header .button {
	@media (min-width: 1100px) {
		background-color: #ff3636;

		&:after {
			display: none;
		}
	}
}

.header .label {
	font-weight: 600;
	font-size: 16px;
	line-height: 52px;
	color: #ffffff;

	padding-left: 45px;
	padding-right: 45px;
	margin: 0;
}

.header .city {
	display: none;

	@media (min-width: 1100px) {
		display: block;
	}
}

.header-links-mobile {
	margin-bottom: 38px;

	@media (min-width: 1100px) {
		display: none;
	}
}

.header-links-mobile li {
	margin-bottom: 24px;

	&:nth-child(1) {
		h2:before {
			background-image: url("../img/src/ambulance-white.png");
		}
	}

	&:nth-child(2) {
		h2:before {
			background-image: url("../img/src/hospital-white.png");
		}
	}

	&:nth-child(3) {
		margin-bottom: 0;

		h2:before {
			background-image: url("../img/src/virus-white.png");
		}
	}
}

.header-links-mobile h2 {
	position: relative;

	padding-left: 44px;

	font-family: ProximaNova;
	font-style: normal;
	font-weight: 600;
	font-size: 16px;
	line-height: 36px;
	color: #ffffff;
	cursor: pointer;

	&::before {
		content: "";

		position: absolute;
		top: 0;
		left: 0;
		transform: translate(0, 0);

		background-position: 0 0;
		background-repeat: no-repeat;

		width: 32px;
		height: 32px;
	}

	&::after {
		content: "";

		position: absolute;
		top: 50%;
		right: 0;
		transform: translate(0, -50%);

		background-position: 0 0;
		background-repeat: no-repeat;
		background-image: url("../img/src/down.png");

		height: 24px;
		width: 24px;
	}
}

.header-links-items {
	display: none;

	margin-top: 37px;
}

.header-links-items a {
	margin-bottom: 30px;

	display: block;

	font-family: ProximaNova;
	font-style: normal;
	font-weight: 300;
	font-size: 18px;
	line-height: 22px;

	&:last-child {
		margin-bottom: 0;
	}

	&:hover {
		text-decoration: underline;
	}
}

.popular-links-mobile h2 {
	font-family: ProximaNova;
	font-weight: bold;
	font-size: 20px;
	line-height: 24px;
	color: #ffffff;

	margin-bottom: 27px;
}

.popular-links-mobile a {
	font-family: ProximaNova;
	font-weight: normal;
	font-size: 18px;
	line-height: 22px;
	color: #ffffff;
	&:hover {
		text-decoration: underline;
	}
}

.popular-links-mobile li {
	margin-bottom: 23px;

	&:last-child {
		margin-bottom: 0;
	}
}

.popular-links-mobile {
	margin-bottom: 38px;

	@media (min-width: 1100px) {
		display: none;
	}
}

.nav {
	@media (max-width: 1099px) {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		font-family: ProximaNova;
		font-style: normal;
		font-weight: normal;
		font-size: 18px;
		line-height: 22px;

		max-width: 250px;
		margin-bottom: 53px;
	}

	@media (min-width: 1100px) {
		display: flex;
		margin-top: 20px;
	}

	@media (min-width: 1280px) {
		margin-top: 0;
	}
}

.nav-column {
	@media (max-width: 1099px) {
		display: flex;
		flex-direction: column;

		a {
			margin-bottom: 13px;

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	@media (min-width: 1100px) {
		display: flex;

		&:last-child {
			a {
				&:last-child {
					margin-right: 0;
				}
			}
		}

		a {
			letter-spacing: 0.5px;
			font-size: 16px;
			line-height: 19px;
			color: #2b3f64;

			margin-right: 150px;

			&:hover {
				color: #365dd6;
				text-decoration: underline;
			}

			@media (min-width: 1280px) {
				margin-right: 10px;
			}

			@media (min-width: 1400px) {
				margin-right: 40px;
			}

			@media (min-width: 1600px) {
				margin-right: 60px;
			}

			@media (min-width: 1700px) {
				margin-right: 80px;
			}
		}
	}
}

.header-callback-mobile {
	position: relative;
	padding-left: 34px;

	&::before {
		content: "";

		position: absolute;
		top: 5px;
		left: 0;
		transform: translate(0, 0);

		background-position: 0 0;
		background-repeat: no-repeat;
		background-image: url("../img/src/phone-white.png");

		width: 24px;
		height: 24px;
	}

	@media (min-width: 1100px) {
		display: none;
	}
}

.header-callback-mobile a {
	font-family: ProximaNova;
	font-weight: bold;
	font-size: 24px;
	line-height: 29px;
}

.header-callback-mobile p {
	font-family: ProximaNova;
	font-style: normal;
	font-weight: normal;
	font-size: 15px;
	line-height: 18px;
	display: flex;
	align-items: center;
}

.header-bottom {
	display: none;

	@media (min-width: 1100px) {
		display: flex;
		justify-content: space-between;
		align-items: center;

		width: 100%;
		padding-left: 16px;
		padding-right: 16px;
	}

	@media (min-width: 1920px) {
		padding-right: 158px;
	}
}

.header-bottom-links {
	max-width: 820px;
	margin-left: auto;
	margin-top: 30px;

	display: flex;
	justify-content: space-between;

	@media (min-width: 1200px) {
		max-width: 898px;
	}
}

.header-bottom-item {
	position: relative;
	width: 33%;
}

.header-bottom-item {
	background-color: #fff;

	&:nth-child(1) {
		width: 374px;

		h2::before {
			background-image: url("../img/src/ambulance.png");
		}

		.header-bottom-sub {
			width: 374px;
			margin-left: -27px;
		}
	}

	&:nth-child(2) {
		width: 366px;

		h2::before {
			background-image: url("../img/src/hospital.png");
		}

		.header-bottom-sub {
			width: 366px;
			margin-left: -27px;
		}
	}

	&:nth-child(3) {
		width: 343px;

		h2::before {
			background-image: url("../img/src/virus.png");
		}

		.header-bottom-sub {
			width: 366px;
			margin-left: -27px;
		}
	}
}

.header-bottom-item h2 {
	padding-bottom: 28px;
	padding-left: 42px;
	position: relative;
	display: inline-block;
	border-bottom: 3px solid transparent;

	font-weight: 500;
	font-size: 18px;
	line-height: 22px;
	color: #2b3f64;
	cursor: pointer;

	&::before {
		content: "";

		position: absolute;
		top: -5px;
		left: 0;
		transform: translate(0, 0);

		background-position: 0 0;
		background-repeat: no-repeat;

		width: 32px;
		height: 32px;
	}
}

.header-bottom-item h3 {
	font-weight: 600;
	font-size: 24px;
	line-height: 29px;
	color: #2b3f64;

	margin-bottom: 24px;
}

.header-bottom-sub {
	position: absolute;
	top: 54px;
	left: 0;
	display: none;
	box-shadow: 0px 5px 20px 2px rgba(56, 55, 121, 0.07);
	background-color: #ffffff;
	z-index: 10;

	padding-top: 36px;
	padding-right: 39px;
	padding-bottom: 26px;
	padding-left: 27px;

	li {
		margin-bottom: 20px;

		font-weight: 300;
		font-size: 16px;
		line-height: 19px;
		color: #2b3f64;

		a:hover {
			text-decoration-line: underline;
			color: #365dd6;
		}

		&:last-child {
			margin-bottom: 0;
		}
	}
}

.header-bottom-sub_block {
	display: block;
}

.call-block {
	position: relative;
	padding-left: 34px;

	&::before {
		content: "";

		position: absolute;
		top: 5px;
		left: 0;
		transform: translate(0, 0);

		background-position: 0 0;
		background-repeat: no-repeat;
		background-image: url("../img/src/local_phone.png");

		width: 24px;
		height: 24px;
	}
}

.call-block a {
	color: #ff3636;
	font-size: 24px;
	line-height: 29px;
	font-weight: bold;
}

.call-block p {
	color: #365dd6;
	font-size: 15px;
	line-height: 18px;
}

//!===============BREADCRUMBS===============//
.breadcrumbs {
	display: none;

	@media (min-width: 1100px) {
		display: flex;

		background: #f7f7f7;
		padding-right: 16px;
		padding-left: 16px;

		margin: 0;
		list-style: none;
	}

	@media (min-width: 1600px) {
		display: flex;
		justify-content: space-between;
	}

	@media (min-width: 1920px) {
		padding-left: 158px;
		padding-right: 158px;
		justify-content: flex-start;
	}
}

.breadcrumbs__item {
	color: #2b3f64;
	font-size: 14px;
	line-height: 19px;
	letter-spacing: 0.3px;

	padding-top: 12px;
	padding-bottom: 12px;
	padding-left: 16px;
	padding-right: 10px;
	margin-right: 45px;

	@media (min-width: 1400px) {
		font-size: 16px;
	}

	@media (min-width: 1600px) {
		margin-right: 20px;
		font-size: 15px;
	}

	@media (min-width: 1920px) {
		margin-right: 40px;
	}

	&:last-child {
		margin-right: 0;
	}

	a:hover {
		text-decoration-line: underline;
		color: #365dd6;
	}
}

.breadcrumbs__item_current {
	background-color: #93aaf0;
	position: relative;

	color: #ffffff;
	font-weight: bold;

	a:hover {
		color: #ffffff;
		text-decoration: none;
	}

	&::after {
		content: "";

		position: absolute;
		top: 0;
		right: -60px;
		transform: translate(0, 0);

		border: 30px solid transparent;
		border-left-color: #93aaf0;

		height: 100%;

		@media (min-width: 1600px) {
			right: -42px;
			border: 21px solid transparent;
			border-left-color: #93aaf0;
		}
	}
}

.branches {
	position: relative;
	display: flex;
	margin-bottom: 10px;

	@media (min-width: 1100px) {
		display: none;
	}

	a {
		max-width: 100%;
		width: 288px;
		height: 52px;
		margin: 0 auto;
		padding-left: 20px;

		background-color: #ff3636;
		border-radius: 4px;
		position: relative;

		text-align: center;
		font-weight: 600;
		font-size: 14px;
		line-height: 55px;
		color: #ffffff;

		&::before {
			content: "";

			position: absolute;
			top: 50%;
			left: 16px;
			transform: translate(0, -50%);

			background-image: url("../img/src/gps.png");
			background-position: 0 0;
			background-repeat: no-repeat;

			height: 16px;
			width: 16px;
		}
	}
}

//!===============CART===============//
.cart .swiper-pagination {
	display: none;

	@media (min-width: 1100px) {
		display: block;
		bottom: 18px;
		left: 50%;

		transform: translateX(-50%);
	}
}

.swiper-pagination-bullet {
	background: #e5e5e5;
	opacity: 1;
	margin-right: 10px;

	&:last-child {
		margin-right: 0;
	}
}

.swiper-pagination-bullet-active {
	background: #365dd6;
}

.cart-slider__car {
	display: none;

	@media (min-width: 1400px) {
		display: block;
	}
}

.cart-inner .description,
.cart-inner .img {
	display: none;

	@media (min-width: 1100px) {
		display: block;
	}
}

.cart-inner {
	margin-right: 27px;
	padding-top: 10px;

	&:first-child {
		padding-top: 0;
		max-width: 501px;

		@media (min-width: 1100px) {
			margin-right: 75px;
		}

		@media (min-width: 1400px) {
			max-width: 610px;
		}

		@media (min-width: 1604px) {
			margin-right: 130px;
		}
	}
}

.cart-inner .description {
	border: 1px solid #93aaf0;
	box-sizing: border-box;
	border-radius: 2px;

	width: 112px;
	height: 28px;
	margin-bottom: 34px;

	font-weight: 300;
	font-size: 13px;
	line-height: 28px;
	color: #365dd6;
	text-align: center;
}

.swiper-button-block {
	position: relative;
	display: flex;
	justify-content: space-between;
	width: 90px;
	margin: 0 auto;

	@media (min-width: 1100px) {
		margin: 0;
		position: absolute;
		bottom: 0;
		left: 0;
	}

	@media (min-width: 1400px) {
		bottom: 45px;
	}
}

.cart-slider__car_truck {
	@media (min-width: 1400px) {
		margin-top: -65px;
	}
}

.cart .swiper-button-prev {
	opacity: 0.5;
}

.cart .swiper-button-prev,
.cart .swiper-button-next {
	position: relative;
	top: auto;
	left: auto;
	right: auto;

	margin: 0;
}

.cart {
	margin-right: 16px;
	margin-left: 16px;
	position: relative;
	margin-bottom: 23px;

	@media (min-width: 550px) {
		margin: 0 auto;
		max-width: 520px;
		margin-top: 10px;

		margin-bottom: 23px;
	}

	padding-right: 12px;
	padding-left: 12px;
	padding-top: 5px;
	padding-bottom: 12px;

	box-shadow: 0px 5px 20px 2px rgba(56, 55, 121, 0.07);
	border-radius: 6px;

	@media (min-width: 1100px) {
		max-width: 1600px;
		margin: 0 auto;
		margin-top: 28px;
		margin-right: 16px;
		margin-left: 16px;
		margin-bottom: 50px;
		padding-top: 17px;
		padding-bottom: 39px;
		padding-left: 47px;
	}

	@media (min-width: 1400px) {
		background-position: right bottom -10px;
		background-repeat: no-repeat;
		background-image: url("../img/Intersect.png");
		padding-bottom: 0;
	}

	@media (min-width: 1604px) {
		background-position: right 100px bottom;
		margin-right: auto;
		margin-left: auto;
	}
}

.cart-slider-item {
	@media (min-width: 1100px) {
		display: flex;
		flex-direction: row-reverse;
		justify-content: flex-end;
	}

	@media (min-width: 1400px) {
		justify-content: space-between;
	}

	@media (min-width: 1604px) {
		justify-content: flex-end;
	}
}

.cart h2 {
	color: #2b3f64;
	font-weight: 600;
	font-size: 20px;
	line-height: 25px;
	text-align: center;

	margin-bottom: 7px;

	@media (min-width: 1100px) {
		text-align: left;
		font-size: 32px;
		line-height: 45px;
	}
}

.cart-need {
