		font-weight: 500;
		font-size: 18px;
		line-height: 22px;
		font-family: Whitney;

		padding-left: 44px;

		&::before {
			content: "";

			position: absolute;
			top: -5px;
			left: 0;
			transform: translate(0, 0);

			background-position: 0 0;
			background-repeat: no-repeat;

			width: 32px;
			height: 32px;
		}
