		flex-direction: column;
		padding-left: 34px;
		position: relative;
		order: -1;

		&::before {
			content: "";

			position: absolute;
			top: 5px;
			left: 0;
			transform: translate(0, 0);

			background-position: 0 0;
			background-repeat: no-repeat;
			background-image: url("../img/src/local_phone.png");

